/* About Section Styles with Enhanced Morphism, 3D Elements, and Historical Theme */
.section-about {
  background-color: var(--color-charcoal);
  position: relative;
  overflow: hidden;
  padding: var(--space-xl) 0;
}

/* Enhanced background patterns with historical influences */
.about-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 20% 30%, rgba(200, 85, 61, 0.05) 0%, transparent 50%),
    radial-gradient(circle at 70% 60%, rgba(200, 85, 61, 0.04) 0%, transparent 40%),
    url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23c8553d' fill-opacity='0.03'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  z-index: 0;
  opacity: 0.6;
}

.about-glow {
  position: absolute;
  top: 20%;
  left: -10%;
  width: 40%;
  height: 60%;
  background: radial-gradient(ellipse, rgba(200, 85, 61, 0.1) 0%, transparent 70%);
  filter: blur(80px);
  z-index: 0;
  opacity: 0.7;
  animation: floatGlow 15s infinite alternate ease-in-out;
}

@keyframes floatGlow {
  0% {
    transform: translateY(0) scale(1);
    opacity: 0.5;
  }
  100% {
    transform: translateY(-50px) scale(1.1);
    opacity: 0.8;
  }
}

/* Section Header Animation with Industrial/Historical Style */
.section-header {
  text-align: center;
  margin-bottom: var(--space-lg);
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.8s ease, transform 0.8s ease;
  position: relative;
  z-index: 2;
}

.section-header.animate-fade-in {
  opacity: 1;
  transform: translateY(0);
}

.section-title {
  font-size: clamp(2rem, 5vw, 3.5rem);
  color: var(--color-parchment);
  margin-bottom: var(--space-sm);
  font-weight: 900;
  letter-spacing: -0.03em;
  text-transform: uppercase;
  font-family: var(--font-secondary);
  position: relative;
}

.section-title-underline {
  height: 4px;
  width: 80px;
  background: linear-gradient(90deg, var(--color-terracotta-dark), var(--color-terracotta), var(--color-terracotta-dark));
  margin: 0 auto;
  border-radius: 2px;
  position: relative;
}

.section-title-underline::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(245, 242, 233, 0.6), transparent);
  animation: shimmer 2s infinite;
}

.section-title::before, .section-title::after {
  content: "â€”";
  font-weight: 300;
  color: var(--color-terracotta);
  position: relative;
  top: -5px;
  margin: 0 15px;
  opacity: 0.7;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(500%);
  }
}

/* About Content Layout - Refined for Better Balance */
.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  grid-template-areas: 
    "portrait bio timeline"
    "skills skills timeline";
  gap: var(--space-lg);
  align-items: start; /* Keep items aligned to the top */
  position: relative;
  z-index: 2;
}

/* Portrait Styling */
.about-portrait-container {
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 1s ease, transform 1s ease;
  position: relative;
  z-index: 2;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  grid-area: portrait;
  min-height: 380px; /* Added minimum height for better grid balance */
}

.about-portrait-container.animate-fade-in-up {
  opacity: 1;
  transform: translateY(0);
}

.portrait-frame {
  position: relative;
  width: 90%;
  max-width: 280px;
  height: auto;
  aspect-ratio: 3/4;
  perspective: 1000px;
  transform-style: preserve-3d;
  transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.portrait-frame::before {
  content: '';
  position: absolute;
  top: -8px;
  left: -8px;
  right: -8px;
  bottom: -8px;
  background-color: rgba(200, 85, 61, 0.05);
  z-index: -1;
  border-radius: 2px;
  transform: translateZ(-10px);
  transition: all 0.5s cubic-bezier(0.16, 1, 0.3, 1);
}

.portrait-frame:hover {
  transform: translateY(-12px) rotateY(-8deg) rotateX(5deg);
}

.portrait-frame:hover::before {
  top: -12px;
  left: -12px;
  right: -12px;
  bottom: -12px;
  background-color: rgba(200, 85, 61, 0.08);
}

.about-portrait-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: sepia(20%) contrast(110%) brightness(95%);
  border-radius: 2px;
  box-shadow: 
    0 10px 25px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(200, 85, 61, 0.2);
  transition: all 0.5s cubic-bezier(0.16, 1, 0.3, 1);
}

.portrait-frame:hover .about-portrait-image {
  filter: sepia(10%) contrast(115%) brightness(100%);
  box-shadow: 
    0 20px 35px rgba(0, 0, 0, 0.4),
    0 0 0 1px rgba(200, 85, 61, 0.3);
}

.portrait-border-accent {
  position: absolute;
  bottom: -10px;
  right: -10px;
  width: 50%;
  height: 50%;
  border-right: 3px solid var(--color-terracotta);
  border-bottom: 3px solid var(--color-terracotta);
  opacity: 0.7;
  z-index: -1;
  transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.portrait-frame:hover .portrait-border-accent {
  width: 65%;
  height: 65%;
  opacity: 1;
  bottom: -15px;
  right: -15px;
}

/* Enhanced Morphism Card Styling with Parchment-like Texture */
.morphism-card {
  background: rgba(26, 26, 26, 0.7);
  backdrop-filter: blur(10px);
  border-radius: 6px;
  border: 1px solid rgba(245, 242, 233, 0.1);
  padding: var(--space-lg);
  box-shadow: 
    0 8px 32px 0 rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(200, 85, 61, 0.1),
    inset 0 0 0 1px rgba(245, 242, 233, 0.05);
  transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  perspective: 1000px;
  will-change: transform, box-shadow;
  grid-area: bio;
  min-height: 380px; /* Added minimum height for better grid balance */
  background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23f5f2e9' fill-opacity='0.02' fill-rule='evenodd'%3E%3Cpath d='M0 0h40v40H0V0zm40 40h40v40H40V40zm0-40h2l-2 2V0zm0 4l4-4h2L40 6V4zm0 4l8-8h2L40 10V8zm0 4L52 0h2L40 14v-2zm0 4L56 0h2L40 18v-2zm0 4L60 0h2L40 22v-2zm0 4L64 0h2L40 26v-2zm0 4L68 0h2L40 30v-2zm0 4L72 0h2L40 34v-2zm0 4L76 0h2L40 38v-2zm0 4L80 0v2L42 40h-2zm4 0L80 4v2L46 40h-2zm4 0L80 8v2L50 40h-2zm4 0l28-28v2L54 40h-2zm4 0l24-24v2L58 40h-2zm4 0l20-20v2L62 40h-2zm4 0l16-16v2L66 40h-2zm4 0l12-12v2L70 40h-2zm4 0l8-8v2l-6 6h-2zm4 0l4-4v2l-2 2h-2z'/%3E%3C/g%3E%3C/svg%3E");
}

.morphism-card:hover {
  box-shadow: 
    0 16px 48px 0 rgba(0, 0, 0, 0.4),
    0 0 0 2px rgba(200, 85, 61, 0.2),
    inset 0 0 0 1px rgba(245, 242, 233, 0.1);
  transform: translateY(-7px) rotateX(3deg) rotateY(-2deg);
}

.morphism-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.03) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: skewX(-25deg);
  transition: left 0.8s ease-out;
  pointer-events: none;
}

.morphism-card:hover::after {
  left: 150%;
}

.card-content {
  position: relative;
  z-index: 2;
}

.morphism-shine {
  position: absolute;
  top: -200%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    135deg,
    transparent 0%,
    transparent 45%,
    rgba(245, 242, 233, 0.1) 50%,
    transparent 55%,
    transparent 100%
  );
  transform: rotate(25deg);
  transition: all 0.8s;
  pointer-events: none;
  z-index: 1;
}

.morphism-card:hover .morphism-shine {
  top: -100%;
  left: -100%;
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

/* About Text Content with Historical Typography */
.about-text {
  opacity: 0;
  transform: translateX(-30px);
  transition: opacity 1s ease, transform 1s ease;
}

.about-text.animate-fade-in-left {
  opacity: 1;
  transform: translateX(0);
}

.about-description {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: var(--space-md);
  color: var(--color-parchment);
  font-family: var(--font-primary);
  position: relative;
  padding-left: 2px;
}

/* Fun Fact styling */
.fun-fact {
  font-size: 0.9rem;
  color: var(--color-stone);
  margin-top: var(--space-sm);
  margin-bottom: var(--space-sm);
  padding-left: 2px;
  font-style: italic;
}

.about-quote {
  font-size: 1.25rem;
  line-height: 1.5;
  font-style: italic;
  color: var(--color-stone);
  padding-left: var(--space-md);
  border-left: 3px solid var(--color-terracotta);
  position: relative;
  margin-top: var(--space-md);
  font-family: Georgia, serif;
  quotes: """ """ "'" "'";
}

.about-quote::before {
  content: open-quote;
  position: absolute;
  top: -20px;
  left: var(--space-md);
  font-size: 4rem;
  color: rgba(200, 85, 61, 0.1);
  font-family: Georgia, serif;
}

/* Enhanced Skills Grid - Now fits in 1 grid cell */
.bento-grid {
  grid-area: skills;
  display: flex;
  height: 100%;
  opacity: 0;
  transform: translateX(30px);
  transition: opacity 1s ease, transform 1s ease;
  margin-bottom: var(--space-lg); /* Added bottom margin for visual balance */
}

/* Skills bento item takes full width now */
.skills-card {
  width: 100%;
}

.bento-item {
  background: var(--color-black-light);
  border-radius: 6px;
  padding: var(--space-md);
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  border-left: 4px solid var(--color-terracotta);
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  transform-style: preserve-3d;
  background-image: url("data:image/svg+xml,%3Csvg width='40' height='40' viewBox='0 0 40 40' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23c8553d' fill-opacity='0.03' fill-rule='evenodd'%3E%3Cpath d='M0 38.59l2.83-2.83 1.41 1.41L1.41 40H0v-1.41zM0 1.4l2.83 2.83 1.41-1.41L1.41 0H0v1.41zM38.59 40l-2.83-2.83 1.41-1.41L40 38.59V40h-1.41zM40 1.41l-2.83 2.83-1.41-1.41L38.59 0H40v1.41zM20 18.6l2.83-2.83 1.41 1.41L21.41 20l2.83 2.83-1.41 1.41L20 21.41l-2.83 2.83-1.41-1.41L18.59 20l-2.83-2.83 1.41-1.41L20 18.59z'/%3E%3C/g%3E%3C/svg%3E");
}

.bento-item:hover {
  transform: translateY(-7px) rotateX(3deg);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(200, 85, 61, 0.2);
  border-left-width: 6px;
}

.bento-item::after {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.03) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  transform: skewX(-25deg);
  transition: left 0.8s ease-out;
  pointer-events: none;
}

.bento-item:hover::after {
  left: 150%;
}

.bento-item h3 {
  font-size: 1.5rem;
  margin-bottom: var(--space-sm);
  color: var(--color-terracotta);
  position: relative;
  z-index: 1;
  font-family: var(--font-secondary);
  letter-spacing: 0.5px;
  transition: color 0.3s ease;
}

.bento-item:hover h3 {
  color: var(--color-terracotta-light);
}

.bento-item h3::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: var(--color-terracotta);
  transform-origin: left;
  transition: width 0.5s cubic-bezier(0.34, 1.56, 0.64, 1), background-color 0.3s ease;
}

.bento-item:hover h3::after {
  width: 80px;
  background-color: var(--color-terracotta-light);
}

/* Skills Grid with Industrial Theme */
.skills-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: var(--space-sm);
}

.skill-category {
  margin-bottom: var(--space-sm);
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  cursor: pointer;
  transform-style: preserve-3d;
  position: relative;
  padding: 8px;
  border-radius: 4px;
}

.skill-category:hover {
  background: rgba(26, 26, 26, 0.2);
}

.skill-category.active {
  transform: translateZ(10px);
  background: rgba(26, 26, 26, 0.4);
}

.skill-category-title {
  font-size: 1rem;
  color: var(--color-parchment);
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  transition: color 0.3s ease;
  font-family: var(--font-secondary);
  letter-spacing: 0.5px;
}

.skill-icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  background-color: var(--color-terracotta);
  margin-right: 8px;
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1), background-color 0.3s ease;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  position: relative;
}

.skill-icon::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background-color: var(--color-black-light);
  clip-path: polygon(50% 15%, 85% 50%, 50% 85%, 15% 50%);
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1), opacity 0.3s ease;
}

.skill-category.active .skill-icon::before,
.skill-category:hover .skill-icon::before {
  transform: scale(0);
  opacity: 0;
}

.skill-category.active .skill-icon,
.skill-category:hover .skill-icon {
  transform: rotate(45deg) scale(1.3);
  background-color: var(--color-terracotta-light);
}

.skill-category.active .skill-category-title,
.skill-category:hover .skill-category-title {
  color: var(--color-terracotta-light);
  transform: translateX(3px);
}

.skill-list {
  list-style-type: none;
  padding: 0;
  transition: all 0.3s ease;
  max-height: 100px;
  overflow: hidden;
}

.skill-list.expanded {
  transform: translateZ(5px);
}

.skill-item {
  font-size: 0.9rem;
  color: var(--color-gray-light);
  margin-bottom: 0.25rem;
  padding-left: 16px;
  position: relative;
  transition: all 0.3s ease;
  transform: translateZ(0);
}

.skill-category.active .skill-item,
.skill-category:hover .skill-item {
  color: var(--color-stone);
  transform: translateX(5px) translateZ(5px);
}

.skill-item::before {
  content: 'â€¢';
  position: absolute;
  left: 0;
  color: var(--color-gray);
  transition: color 0.3s ease;
}

.skill-category.active .skill-item::before,
.skill-category:hover .skill-item::before {
  color: var(--color-terracotta);
}

/* ===== Timeline Styling - Refined ===== */
.timeline-container {
  grid-area: timeline;
  opacity: 0;
  transform: translateY(30px);
  transition: all 1s ease;
  position: relative;
  padding: var(--space-md) var(--space-sm);
  background: rgba(26, 26, 26, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 6px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.2),
    inset 0 0 0 1px rgba(200, 85, 61, 0.15);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: var(--space-lg); /* Added bottom margin for visual balance */
  max-height: 95vh; /* Prevent excessive height */
  overflow: hidden; /* Contain its contents */
}

.timeline-container.animate-fade-in-right {
  opacity: 1;
  transform: translateY(0);
}

.timeline-title {
  font-size: 1.8rem;
  color: var(--color-terracotta);
  margin-bottom: var(--space-lg);
  text-align: center;
  font-family: var(--font-secondary);
  letter-spacing: 1px;
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Industrial-style title decoration with rivets */
.timeline-title::before, 
.timeline-title::after {
  content: '';
  width: 8px;
  height: 8px;
  background-color: var(--color-terracotta);
  border-radius: 50%;
  position: absolute;
  box-shadow:
    0 0 0 2px rgba(26, 26, 26, 0.7),
    inset 0 0 0 1px rgba(0, 0, 0, 0.3);
}

.timeline-title::before {
  left: 10%;
  top: 50%;
}

.timeline-title::after {
  right: 10%;
  top: 50%;
}

/* Industrial divider with rivet and plate look */
.timeline-title-divider {
  position: relative;
  width: 120px;
  height: 2px;
  background: linear-gradient(90deg, 
    transparent, 
    var(--color-terracotta-dark), 
    var(--color-terracotta), 
    var(--color-terracotta-dark), 
    transparent
  );
  margin-top: 10px;
  border-radius: 1px;
}

.timeline-title-divider::before,
.timeline-title-divider::after {
  content: '';
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: var(--color-terracotta);
  top: -2px;
  box-shadow:
    0 0 0 1px rgba(0, 0, 0, 0.3),
    inset 0 0 0 1px rgba(26, 26, 26, 0.3);
}

.timeline-title-divider::before {
  left: 10px;
}

.timeline-title-divider::after {
  right: 10px;
}

.timeline {
  position: relative;
  padding: var(--space-md) 0;
  margin-left: 20px;
  flex-grow: 1;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: var(--color-terracotta) var(--color-black-light);
  mask-image: linear-gradient(to bottom, 
    transparent, 
    black 5%, 
    black 95%, 
    transparent
  );
  -webkit-mask-image: linear-gradient(to bottom, 
    transparent, 
    black 5%, 
    black 95%, 
    transparent
  );
  padding-top: 10px;
  padding-bottom: 10px;
}

.timeline::-webkit-scrollbar {
  width: 6px;
}

.timeline::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.timeline::-webkit-scrollbar-thumb {
  background-color: var(--color-terracotta-dark);
  border-radius: 10px;
}

/* Industrialized Timeline Track/Line */
.timeline-track {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 16px;
  width: 2px;
  transform: translateX(-50%);
  z-index: 5;
  background: none; /* Remove standard background */
  overflow: visible;
}

/* Industrial pipeline/conduit effect with metallic texture */
.timeline-track::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: 
    /* Metallic gradient for pipe */
    linear-gradient(
      to right,
      var(--color-charcoal) 0%,
      var(--color-terracotta-dark) 30%,
      var(--color-terracotta) 50%,
      var(--color-terracotta-dark) 70%,
      var(--color-charcoal) 100%
    ),
    /* Vertical segments for industrial pipe joints */
    repeating-linear-gradient(
      to bottom,
      rgba(26, 26, 26, 0.9) 0px,
      rgba(26, 26, 26, 0.9) 2px,
      rgba(200, 85, 61, 0.2) 2px,
      rgba(200, 85, 61, 0.2) 4px,
      rgba(26, 26, 26, 0.9) 4px,
      rgba(26, 26, 26, 0.9) 20px
    );
  z-index: 1;
  box-shadow: 
    0 0 8px rgba(0, 0, 0, 0.3),
    inset 0 0 2px rgba(255, 255, 255, 0.1);
}

/* Add subtle "steam/energy" animation that travels down the timeline */
.timeline-track::after {
  content: '';
  position: absolute;
  top: -100%;
  left: -3px;
  right: -3px;
  height: 40%;
  background: linear-gradient(
    to bottom, 
    rgba(200, 85, 61, 0), 
    rgba(245, 242, 233, 0.3),
    rgba(200, 85, 61, 0)
  );
  animation: timelineEnergy 8s infinite ease-in-out;
  opacity: 0.6;
  pointer-events: none;
  filter: blur(2px);
  z-index: 2;
}

@keyframes timelineEnergy {
  0% {
    top: -40%;
    opacity: 0;
  }
  10% {
    opacity: 0.6;
  }
  90% {
    opacity: 0.6;
  }
  100% {
    top: 100%;
    opacity: 0;
  }
}

/* Industrial caps for the timeline pipe */
.timeline-cap-top,
.timeline-cap-bottom {
  position: absolute;
  left: 16px;
  width: 10px;
  height: 10px;
  background-color: var(--color-terracotta);
  transform: translateX(-50%);
  z-index: 6;
  border-radius: 2px;
  box-shadow: 
    0 0 0 2px rgba(26, 26, 26, 0.7),
    0 0 10px rgba(0, 0, 0, 0.3);
}

.timeline-cap-top {
  top: 0;
}

.timeline-cap-bottom {
  bottom: 0;
}

.timeline-cap-top::after,
.timeline-cap-bottom::after {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  background-color: rgba(26, 26, 26, 0.8);
  border-radius: 50%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 2px rgba(255, 255, 255, 0.2);
}

/* Timeline items with improved spacing for readability */
.timeline-item {
  position: relative;
  margin-bottom: var(--space-md);
  padding-left: 42px;
  padding-bottom: 10px; /* Increased spacing between items */
  opacity: 0;
  transform: translateX(-20px);
  transition: opacity 0.75s ease, transform 0.75s cubic-bezier(0.16, 1, 0.3, 1);
}

.timeline-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}

.timeline-item.visible {
  opacity: 1;
  transform: translateX(0);
}

/* Industrial rivet/gear style timeline markers */
.timeline-marker {
  position: absolute;
  top: 8px; /* Slightly adjusted for better alignment */
  left: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: var(--color-black-light);
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(-50%);
  z-index: 10;
  box-shadow: 
    0 0 0 2px rgba(26, 26, 26, 0.9),
    0 0 10px rgba(0, 0, 0, 0.3);
  transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
}

/* Industrial seal/rivet design for the markers */
.timeline-marker::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background: 
    radial-gradient(
      circle at center,
      var(--color-terracotta-dark) 0%,
      var(--color-terracotta) 30%,
      var(--color-terracotta-dark) 70%,
      var(--color-charcoal) 100%
    );
  opacity: 0.9;
  z-index: -1;
}

/* Rivets design around the marker */
.timeline-marker::after {
  content: '';
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  border-radius: 50%;
  background: transparent;
  border: 2px dashed var(--color-terracotta-dark);
  opacity: 0.6;
  z-index: -2;
}

.timeline-item:hover .timeline-marker {
  transform: translateX(-50%) scale(1.1);
  box-shadow: 
    0 0 0 2px rgba(26, 26, 26, 0.9),
    0 0 15px rgba(200, 85, 61, 0.4);
}

.timeline-item:hover .timeline-marker::after {
  animation: rotateMarker 8s linear infinite;
}

@keyframes rotateMarker {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Industrial gear design for marker inner */
.marker-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: rgba(26, 26, 26, 0.8);
  color: var(--color-terracotta);
  position: relative;
  transition: all 0.3s ease;
  box-shadow: 
    inset 0 0 5px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(200, 85, 61, 0.3);
}

.marker-inner::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  background: var(--color-terracotta);
  border-radius: 50%;
  z-index: 1;
  box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.4);
  transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

/* Gear teeth pattern using CSS */
.marker-inner::after {
  content: '';
  position: absolute;
  top: -6px;
  left: -6px;
  right: -6px;
  bottom: -6px;
  background: 
    conic-gradient(
      from 0deg,
      rgba(200, 85, 61, 0) 0deg,
      rgba(200, 85, 61, 0) 30deg,
      var(--color-terracotta) 30deg,
      var(--color-terracotta) 60deg,
      rgba(200, 85, 61, 0) 60deg,
      rgba(200, 85, 61, 0) 90deg,
      var(--color-terracotta) 90deg,
      var(--color-terracotta) 120deg,
      rgba(200, 85, 61, 0) 120deg,
      rgba(200, 85, 61, 0) 150deg,
      var(--color-terracotta) 150deg,
      var(--color-terracotta) 180deg,
      rgba(200, 85, 61, 0) 180deg,
      rgba(200, 85, 61, 0) 210deg,
      var(--color-terracotta) 210deg,
      var(--color-terracotta) 240deg,
      rgba(200, 85, 61, 0) 240deg,
      rgba(200, 85, 61, 0) 270deg,
      var(--color-terracotta) 270deg,
      var(--color-terracotta) 300deg,
      rgba(200, 85, 61, 0) 300deg,
      rgba(200, 85, 61, 0) 330deg,
      var(--color-terracotta) 330deg,
      var(--color-terracotta) 360deg
    );
  mask: radial-gradient(transparent 40%, black 40%);
  -webkit-mask: radial-gradient(transparent 40%, black 40%);
  border-radius: 50%;
  opacity: 0.6;
  z-index: 0;
  transition: all 0.3s ease;
}

.timeline-item:hover .marker-inner::before {
  transform: translate(-50%, -50%) scale(1.2);
  background: var(--color-terracotta-light);
}

.timeline-item:hover .marker-inner::after {
  animation: rotateGear 10s linear infinite;
  opacity: 0.8;
}

@keyframes rotateGear {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

/* Specific marker styling for different item types */
.timeline-item.education .marker-inner {
  background-color: rgba(245, 242, 233, 0.1);
}

.timeline-item.education .marker-inner::before {
  background: var(--color-stone);
}

.timeline-item.project .marker-inner {
  background: rgba(26, 26, 26, 0.9);
}

.timeline-item.research .timeline-marker::after {
  border-style: dotted;
}

.timeline-item.future .timeline-marker {
  opacity: 0.8;
}

.timeline-item.future .timeline-marker::before {
  opacity: 0.7;
}

/* Blueprint-style timeline content styling */
.timeline-content {
  background: rgba(26, 26, 26, 0.7);
  border-radius: 4px;
  padding: var(--space-md);
  transition: all 0.4s cubic-bezier(0.16, 1, 0.3, 1);
  transform-origin: left center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  /* Blueprint grid texture */
  background-image: 
    linear-gradient(
      rgba(245, 242, 233, 0.03) 1px, 
      transparent 1px
    ),
    linear-gradient(
      90deg, 
      rgba(245, 242, 233, 0.03) 1px, 
      transparent 1px
    );
  background-size: 15px 15px;
  border-left: 3px solid var(--color-terracotta);
}

/* Timeline content hover effects */
.timeline-content::after {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 70%;
  height: 100%;
  background: linear-gradient(
    to right,
    rgba(245, 242, 233, 0) 0%,
    rgba(245, 242, 233, 0.05) 50%,
    rgba(245, 242, 233, 0) 100%
  );
  transform: skewX(-25deg);
  transition: left 0.7s cubic-bezier(0.16, 1, 0.3, 1);
  pointer-events: none;
}

.timeline-item:hover .timeline-content::after {
  left: 150%;
}

.timeline-item:hover .timeline-content {
  transform: translateX(5px) scale(1.01);
  background-color: rgba(26, 26, 26, 0.8);
  box-shadow: 
    0 8px 20px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(200, 85, 61, 0.2);
}

/* Future items styling with dashed blueprint look */
.timeline-item.future .timeline-content {
  border-left-style: dashed;
  background-color: rgba(26, 26, 26, 0.6);
  background-image: 
    linear-gradient(
      rgba(245, 242, 233, 0.02) 1px, 
      transparent 1px
    ),
    linear-gradient(
      90deg, 
      rgba(245, 242, 233, 0.02) 1px, 
      transparent 1px
    );
}

/* Industrial stamp-like year badge */
.timeline-year {
  display: inline-block;
  padding: 5px 12px;
  background: var(--color-terracotta);
  color: var(--color-parchment);
  font-size: 0.9rem;
  font-weight: 700;
  border-radius: 2px;
  margin-bottom: 12px;
  font-family: var(--font-secondary);
  letter-spacing: 1px;
  position: relative;
  box-shadow: 
    inset 0 1px 0 rgba(255, 255, 255, 0.2),
    inset 0 -1px 0 rgba(0, 0, 0, 0.2),
    0 2px 4px rgba(0, 0, 0, 0.2);
  transition: all 0.3s cubic-bezier(0.16, 1, 0.3, 1);
  text-transform: uppercase;
  /* Stamp texture */
  background-image: 
    linear-gradient(
      to right,
      rgba(26, 26, 26, 0.1),
      transparent 10%,
      transparent 90%,
      rgba(26, 26, 26, 0.1)
    );
}

/* Year badge rivets */
.timeline-year::before,
.timeline-year::after {
  content: '';
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(26, 26, 26, 0.5);
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}

.timeline-year::before {
  left: 4px;
}

.timeline-year::after {
  right: 4px;
}

.timeline-item:hover .timeline-year {
  transform: translateY(-1px);
  box-shadow: 
    inset 0 1px 0 rgba(255, 255, 255, 0.3),
    inset 0 -1px 0 rgba(0, 0, 0, 0.3),
    0 3px 6px rgba(0, 0, 0, 0.3);
}

.timeline-item.future .timeline-year {
  background-color: var(--color-terracotta-dark);
  opacity: 0.9;
}

/* Enhanced typography for timeline content */
.timeline-item-title {
  font-size: 1.2rem;
  color: var(--color-parchment);
  margin-bottom: 8px;
  font-family: var(--font-secondary);
  letter-spacing: 0.5px;
  font-weight: 700;
  line-height: 1.3;
  position: relative;
  display: inline-block;
}

/* Blueprint underline effect */
.timeline-item-title::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(
    to right,
    var(--color-terracotta-dark),
    var(--color-terracotta),
    var(--color-terracotta-dark)
  );
  transform: scaleX(0.6);
  transform-origin: left;
  transition: transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.timeline-item:hover .timeline-item-title::after {
  transform: scaleX(1);
}

/* Institution name with improved contrast and historical styling */
.timeline-institution {
  font-size: 0.95rem;
  color: var(--color-stone);
  margin-bottom: 12px;
  font-style: italic;
  position: relative;
  padding-left: 16px; /* Increased for better readability */
  line-height: 1.5;
  display: block;
}

.timeline-institution::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 8px;
  height: 2px;
  background: var(--color-terracotta);
  opacity: 0.8;
}

/* Enhanced description text readability */
.timeline-description {
  font-size: 1rem; /* Increased for better readability */
  line-height: 1.6;
  color: var(--color-gray-light);
  letter-spacing: 0.2px;
  margin-top: 8px; /* Added spacing between institution and description */
}

/* Media query for reduced motion */
@media (prefers-reduced-motion: reduce) {
  .about-glow,
  .timeline-track::after,
  .timeline:hover .timeline-track::before,
  .morphism-card::after,
  .bento-item::after,
  .timeline-item:hover .timeline-content::after,
  .timeline-item:hover .marker-inner::after,
  .timeline-item:hover .timeline-year::after,
  .timeline-year::after {
    animation: none;
    transition: none;
    display: none;
  }
  
  .section-title-underline::after,
  .morphism-shine {
    animation: none;
    display: none;
  }
  
  .portrait-frame:hover,
  .morphism-card:hover,
  .bento-item:hover,
  .skill-category.active .skill-item,
  .skill-category:hover .skill-item,
  .skill-category.active .skill-category-title,
  .skill-category:hover .skill-category-title,
  .skill-category.active .skill-icon,
  .skill-category:hover .skill-icon,
  .timeline-item:hover .timeline-content,
  .timeline-item:hover .timeline-marker,
  .timeline-item:hover .marker-inner::before {
    transform: none;
    transition: none;
  }
  
  .timeline-item.visible {
    opacity: 1;
    transform: none;
    transition: none;
  }
}

/* Responsive Adjustments */
@media (max-width: 1200px) {
  .skills-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 992px) {
  .about-content {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
      "portrait bio"
      "timeline timeline"
      "skills skills";
    gap: var(--space-md);
  }
  
  .timeline-container {
    margin-top: var(--space-md);
    max-height: 70vh; /* Adjusted height for responsive view */
  }
  
  .about-portrait-container,
  .morphism-card {
    min-height: auto; /* Reset min-height for smaller screens */
  }
}

@media (max-width: 768px) {
  .about-content {
    grid-template-columns: 1fr;
    grid-template-areas: 
      "portrait"
      "bio"
      "timeline"
      "skills";
  }
  
  .about-portrait-container {
    justify-self: center;
  }
  
  .portrait-frame {
    max-width: 240px;
  }
  
  .skills-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .timeline-container {
    max-height: 60vh; /* Further adjusted for mobile */
  }
}

@media (max-width: 576px) {
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .timeline-container {
    padding: var(--space-xs);
  }
  
  .timeline {
    margin-left: 15px;
  }
  
  .timeline-item {
    padding-left: 30px;
  }
  
  .timeline-marker {
    width: 26px;
    height: 26px;
  }
  
  .marker-inner {
    width: 18px;
    height: 18px;
  }
}

/* Media query for reduced motion */
@media (prefers-reduced-motion: reduce) {
  .timeline-track::after,
  .timeline-item:hover .marker-inner::after,
  .timeline-item:hover .timeline-marker::after {
    animation: none;
  }
}
